import React from "react";
import "./FormationExp.css";
import DataFormations from "../datas/Formations";
import Formations from "./Formations";
import DataExperiences from "../datas/Experiences";
import Experiences from "./Experiences";
import Diplome from "./Diplome";
import dataDiplome from "../datas/Diplome";
import ProjetRealise from "./ProjetRealise";
import DataProjet from "../datas/ProjetRealise";
import President from "./President"
import dataPresident from '../datas/President'


function FormationExp() {
  return (
    <>
      <President datas={dataPresident} />
      <Experiences datas={DataExperiences} />
      <Formations datas={DataFormations} />
      <Diplome datas={dataDiplome} />
      <ProjetRealise datas={DataProjet} />
    </>
  );
}

export default FormationExp;
